import {
  Button,
  Container,
  Col,
  Form,
  Modal,
  Row,
  Table,
} from "react-bootstrap";
import { useState } from "react";

import { ReactComponent as TrashIcon } from "bootstrap-icons/icons/trash.svg";
import { ReactComponent as EditIcon } from "bootstrap-icons/icons/pencil.svg";

import ScrollToBottom from "react-scroll-to-bottom";

import Swal from "sweetalert2";

import "./App.css";

function App() {
  const [showModal, setShowModal] = useState(false);
  const [input, setInput] = useState("");
  const [editItem, setEditItem] = useState("");
  const [editIndex, setEditIndex] = useState("");
  const items = JSON.parse(localStorage.getItem("items")) || [];

  const add_item = () => {
    if (input) {
      items.push(input);
      localStorage.setItem("items", JSON.stringify(items));
      setInput("");
    } else {
      console.log("empty");
    }
  };

  function handleCloseModal() {
    setShowModal(false);
  }

  const edit_item = (item, index) => {
    setShowModal(true);
    setEditItem(item);
    setEditIndex(index);
  };

  const save_item = () => {
    const updatedItems = items.map((item, index) =>
      index === editIndex ? editItem : item
    );

    localStorage.setItem("items", JSON.stringify(updatedItems));

    setShowModal(false);
  };

  const delete_item = (index) => {
    items.splice(index, 1);
    localStorage.setItem("items", JSON.stringify(items));

    document.getElementById(`item-${index}`).remove();

    Swal.fire({
      icon: "success",
      title: "Item Deleted",
    });
  };

  const clear_all = () => {
    localStorage.clear();

    window.location.reload();
  };

  return (
    <Container className="min-vh-100 align-items-center justify-content-center d-flex">
      <Row className="shopping-border px-3">
        <Col xs={12}>
          <h3 className="text-center m-2">Shopping List</h3>
        </Col>
        <Col xs={12}>
          <Form.Group className="text-center">
            <Form.Control
              className="m-2"
              type="text"
              value={input}
              placeholder="Input items"
              onKeyPress={(e) => {
                e.key === "Enter" && add_item();
              }}
              onChange={(e) => setInput(e.target.value)}
            />
            <Button className="m-2" onClick={add_item} variant="success">
              Add Item
            </Button>
            <Button className="m-2" onClick={clear_all} variant="danger">
              Clear All
            </Button>
          </Form.Group>
        </Col>
        <Col xs={12} className="text-center m-2">
          <ScrollToBottom className="message-container">
            <Table striped bordered hover>
              <thead>
                <tr>
                  <th colSpan={2}>Items</th>
                </tr>
              </thead>
              <tbody>
                {items.map((item, index) => (
                  <tr key={index} id={`item-${index}`}>
                    <td>{item}</td>
                    <td className="col-sm-2">
                      <EditIcon
                        className="icon"
                        cursor="pointer"
                        onClick={() => edit_item(item, index)}
                      />
                      <TrashIcon
                        className="mx-1 icon"
                        cursor="pointer"
                        onClick={() => delete_item(index)}
                      />
                    </td>
                  </tr>
                ))}
              </tbody>
            </Table>
          </ScrollToBottom>
          <Modal show={showModal} onHide={handleCloseModal}>
            <Modal.Header closeButton>
              <Modal.Title>Edit Item</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <Form.Group controlId="formQuantity">
                <Form.Label>Item</Form.Label>
                <Form.Control
                  type="text"
                  value={editItem}
                  onChange={(e) => setEditItem(e.target.value)}
                />
              </Form.Group>
            </Modal.Body>
            <Modal.Footer>
              <Button variant="success" onClick={handleCloseModal}>
                Close
              </Button>
              <Button variant="danger" onClick={save_item}>
                Save
              </Button>
            </Modal.Footer>
          </Modal>
        </Col>
      </Row>
    </Container>
  );
}

export default App;
